import React, { useRef, useState, useEffect, useCallback, Dispatch, SetStateAction, ReactNode } from 'react'
import { createChart, IChartApi } from 'lightweight-charts'
import { darken } from 'polished'
import Card from '../Card'
import styled from 'styled-components'
import useTheme from 'hooks/useTheme'
import { formatDollarAmount } from 'utils/numbers'
import usePrevious from 'hooks/usePrevious'

const Wrapper = styled(Card)`
  width: 100%;
  padding: 1rem;
  position: relative;
  display: flex;
  background-color: ${({ theme }) => theme.bg0}
  flex-direction: column;
  > * {
    font-size: 1rem;
  }
`

const DEFAULT_HEIGHT = 300

export type LineChartProps = {
  data: any[]
  color?: string | undefined
  height?: number | undefined
  minHeight?: number
  setValue?: Dispatch<SetStateAction<number | undefined>> // used for value on hover
  topLeft?: ReactNode | undefined
  topRight?: ReactNode | undefined
  bottomLeft?: ReactNode | undefined
  bottomRight?: ReactNode | undefined
} & React.HTMLAttributes<HTMLDivElement>

const LineChart = ({
  data,
  color = '#56B2A4',
  setValue,
  height = DEFAULT_HEIGHT,
  minHeight = DEFAULT_HEIGHT,
  ...rest
}: LineChartProps) => {
  // theming
  const theme = useTheme()
  const textColor = theme.text2

  // chart pointer
  const chartRef = useRef<HTMLDivElement>(null)
  const [chartCreated, setChart] = useState<IChartApi | undefined>()

  const dataPrev = usePrevious(data)
  useEffect(() => {
    if (data !== dataPrev && chartCreated) {
      // remove the tooltip element
      const tooltip = document.getElementById('tooltip-id')
      if (chartRef?.current && tooltip) {
        chartRef.current?.removeChild(tooltip)
      }
      chartCreated.resize(0, 0)
      setChart(undefined)
    }
  }, [chartCreated, data, dataPrev])

  // for reseting value on hover exit
  const currentValue = data[data.length - 1]?.value

  const handleResize = useCallback(() => {
    if (chartCreated && chartRef?.current?.parentElement) {
      chartCreated.resize(chartRef.current.parentElement.clientWidth - 32, height)
      chartCreated.timeScale().fitContent()
      chartCreated.timeScale().scrollToPosition(0, false)
    }
  }, [chartCreated, chartRef, height])

  // add event listener for resize
  const isClient = typeof window === 'object'
  useEffect(() => {
    if (!isClient) {
      return
    }
    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [isClient, chartRef, handleResize]) // Empty array ensures that effect is only run on mount and unmount

  // if chart not instantiated in canvas, create it
  useEffect(() => {
    if (!chartCreated && data && !!chartRef?.current?.parentElement) {
      const chart = createChart(chartRef.current, {
        height: height,
        width: chartRef.current.parentElement.clientWidth - 32,
        layout: {
          backgroundColor: 'transparent',
          textColor: textColor,
          fontFamily: 'Inter',
        },
        rightPriceScale: {
          scaleMargins: {
            top: 0.1,
            bottom: 0.1,
          },
          borderVisible: false,
        },
        timeScale: {
          borderVisible: false,
        },
        watermark: {
          color: 'rgba(0, 0, 0, 0)',
        },
        grid: {
          horzLines: {
            visible: false,
          },
          vertLines: {
            visible: false,
          },
        },
        crosshair: {
          horzLine: {
            visible: true,
            style: 3,
            width: 1,
            color: '#505050',
            labelBackgroundColor: color,
          },
          vertLine: {
            visible: true,
            style: 3,
            width: 1,
            color: '#505050',
            labelBackgroundColor: color,
          },
        },
      })
      chart.timeScale().fitContent()
      setChart(chart)
    }
  }, [color, chartCreated, currentValue, data, height, setValue, textColor, theme])

  const setLastBarText = useCallback(() => {
    const toolTip = document.getElementById('tooltip-id')
    if (toolTip) {
      toolTip.innerHTML =
        `<div style="font-size: 22px; margin: 4px 0px; color: ${textColor}">` +
        formatDollarAmount(data?.[data?.length - 1]?.value) +
        '</div>'
    }
  }, [data, textColor])

  useEffect(() => {
    if (chartCreated && data) {
      const series = chartCreated.addAreaSeries({
        lineColor: color,
        topColor: darken(0.3, color),
        bottomColor: theme.bg0,
        lineWidth: 2,
        priceLineVisible: false,
      })
      series.setData(data)
      chartCreated.timeScale().fitContent()
      chartCreated.timeScale().scrollToRealTime()

      const toolTip = document.createElement('div')
      toolTip.setAttribute('id', 'tooltip-id')
      toolTip.className = 'three-line-legend-dark'
      if (chartRef && chartRef.current) {
        chartRef.current.appendChild(toolTip)
      }
      toolTip.style.display = 'block'
      toolTip.style.fontWeight = '500'
      toolTip.style.left = -4 + 'px'
      toolTip.style.top = '-' + 8 + 'px'
      toolTip.style.backgroundColor = 'transparent'

      // update the title when hovering on the chart
      chartCreated.subscribeCrosshairMove(function (param) {
        if (
          chartRef?.current &&
          (param === undefined ||
            param.time === undefined ||
            (param && param.point && param.point.x < 0) ||
            (param && param.point && param.point.x > chartRef.current.clientWidth) ||
            (param && param.point && param.point.y < 0) ||
            (param && param.point && param.point.y > height))
        ) {
          setLastBarText()
        } else if (series && param) {
          const price = parseFloat(param?.seriesPrices?.get(series)?.toString() ?? currentValue)
          toolTip.innerHTML =
            `<div style="font-size: 22px; margin: 4px 0px; color: ${textColor}">` + formatDollarAmount(price) + '</div>'
        }
      })
    }
  }, [chartCreated, color, currentValue, setLastBarText, data, height, setValue, textColor, theme.bg0])

  return (
    <Wrapper minHeight={minHeight}>
      <div ref={chartRef} id={'test-id'} {...rest} />
    </Wrapper>
  )
}

export default LineChart
